<script lang="ts">
  import {Dream} from '../../application/domain/Dream'
  import {container} from '../../framework/inversify.config'
  import DREAM_SERVICE_TYPES from '../../application/service/DreamService'
  import SplashDream from './Dream.svelte'

  let dreams: Dream[] = []

  function showDream() {
    dreams = container.get(DREAM_SERVICE_TYPES.DreamService)
      .getCurrentDreams(10)
    // console.log(dreams)
  }

  let clear
  $: {
    clearInterval(clear)
    clear = setInterval(showDream, 1000)
  }


</script>

<h3> Current Dreams!</h3>
{#each dreams as dream, index}
    <SplashDream dream={dream}/>
{/each}


<style>
    h3 {
        color: #0000ff;
        font-size: 2em;
        font-weight: 20;
    }
</style>
