<script lang="ts">
  import {StringId} from '../../application/domain/Common'
  import {Dream} from '../../application/domain/Dream'
  import {Sex, User} from '../../application/domain/User'
  import DREAM_SERVICE_TYPES from '../../application/service/DreamService'
  import {container} from '../../framework/inversify.config'

  let name: string = 'initial'
  let message: string = 'message'

  function register() {
    if (!name || !name.trim() || !message || !message.trim()) {
      return
    }

    let dream = new Dream(
      new User(StringId.of(name), Sex.MALE),
      message
    )
    console.log(dream)
    container.get(DREAM_SERVICE_TYPES.DreamService).postDream(dream)
    name = ''
    message = ''
  }
</script>

<h3>
    Write New Dream
</h3>
<label for="new-dream-name">ニックネーム: </label>
<input id="new-dream-name" bind:value={name}>
<br>
<label for="new-dream-message">メッセージ: </label>
<textarea id="new-dream-message" cols="40" rows="5" wrap="hard" bind:value={message}></textarea>
<br>
<button on:click={register}>夢を語る</button>

<style>
    h3 {
        color: #ffee55;
        font-size: 2em;
        font-weight: 20;
    }
</style>
